.Contenido
{
  width: 100%;
  height: 90vh;
  display: flex;
  flex-wrap: wrap;
}

/*Principal*/

.Cont-apartado
{
  display: flex;
  width: 33.3%;
  height: 100%;
  text-align: center;
  justify-content: center;
  align-items: center;
  cursor:pointer;
}

.Cont-hover
{
  filter:brightness(0.3);
}

.Casas
{
  background-image: url("images/Casas2.webp");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
}

.Spells
{
  background-image: url("images/Spells2.webp");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
}

.Potions
{
  background-image: url("images/potions.webp");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
}

/*Casas*/

.Cont-Casas
{

  width: 100%;
  display: flex;
  align-items: center;

}

.Cont-Casas-casa
{
  width: 50%;
  height: 100%;
  text-align: center;
  cursor:pointer;
}

.Gry
{
  background-image: url("images/gryffindor.webp");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;

}

.Sly
{
  background-image: url("images/Slythering.webp");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;

}

.Huff
{
  background-image: url("images/hufflepuff.webp");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
}

.Raven
{
  background-image: url("images/ravenclaw.webp");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
}

/*Casa*/

.Cont-Casa
{
  font-size: 50px;
  width: 100%;
}

.Cont-Casa-NombreCasa
{
  width: 100%;
  text-align: center;
  margin-top: 100px;
  margin-bottom: 10px;
  font-size: 5vw;
  font-family: 'Harry Potter', sans-serif;                                              
  
}

.Cont-Casa-CreadorCasa
{
  width: 100%;
  text-align: center;
  font-size: 2vw;
  font-family: 'Harry Potter', sans-serif;                                              

}

.Cont-Casa-Secundario
{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.Cont-Casa-Secundario-Fila1, .Cont-Casa-Secundario-Fila2, .Cont-Casa-Secundario-Fila3
{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
    align-items: center;
}

.Cont-Casa-ColoresCasa, .Cont-Casa-AnimalCasa, .Cont-Casa-JefesCasa
{
  width: 40%;
  text-align: center;
  margin-top: 200px;
}

.Cont-Casa-ElementoCasa, .Cont-Casa-FantasmaCasa, .Cont-Casa-SalaComunCasa, .Cont-Casa-Valores
{
  width: 30%;
  text-align: center;
  margin-top: 200px;
}

.Cont-casa-JefesCasa-Nomnre, .Cont-casa-Valores-valor
{
  width: 100%;
  text-align: center;
  margin-top: 15px;
}

.Cont-Casa-Gry
{
  height: 100%;
  background-image: url("images/gryffindor_fondo.webp");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
  color: #b7931a;
  text-shadow: 1px 1px 10px #1a1919;  

}

.Cont-Casa-Sly
{
  height: 100%;
  background-image: url("images/Slythering_fondo.webp");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
  color: #9e9e9e;
  text-shadow: 1px 1px 10px #1a1919;  

}

.Cont-Casa-Huff
{
  height: 100%;
  background-image: url("images/hufflepuff_fondo.webp");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
  color: #040607;
}

.Cont-Casa-Raven
{
  height: 100%;
  background-image: url("images/ravenclaw_fondo.webp");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
  color: #b58a2d;
  text-shadow: 1px 1px 10px #1a1919;  

}

/*Spells*/

.Cont-Spells
{
  width: 100%;
  height: fit-content;
  min-height: 100vh;
  /*background-image: url("images/spells_fondo.webp");*/
  /*background-image: url("images/spells_fondo2.png");*/
  background:
  linear-gradient(45deg, #343702 0%, #184500 20%, #187546 30%, #006782 40%, #0b1284 50%, #760ea1 60%, #83096e 70%, #840b2a 80%, #b13e12 90%, #e27412 100%);
  background-size: 470px 470px, 970px 970px, 410px 410px, 610px 610px, 530px 530px, 730px 730px, 100% 100%;
  background-color: #840b2a;  background-size:cover;
  background-position-x: center;
  background-position-y: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Cont-Spells-Cards
{
  width: 80%;
  height: 100%;
  min-height: 30vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  border: 1px solid rgba( 23, 8, 90, 0.3 );
  border-radius: 20px;
  background: rgba( 23, 8, 90, 0.3 );
  margin-bottom: 100px;
}

.Cont-Spells-SpellCard
{
  width: 300px;
  margin: 50px;
  height: fit-content;
  border-radius: 10px;
  border: 1px solid wheat;
  text-align: center;
  font-weight: 900;
  font-size: 20px;
  cursor: pointer;
}
.Cont-Spells-SpellCard-Oculto
{
  display: none;
  width: 100%;
  background-color: azure;
  padding-bottom: 30px;
  padding-top: 30px;
  border-radius: 0px 0px 5px 5px;
  color:#040607 !important;
  border-top: 1px solid black;
}
.Cont-Spells-SpellCard-Text
{
  margin: 15px;
}
.Cont-Spells-SpellCard-Text div
{
  font-weight: 100 !important;
}

.Spell-Tipo
{
  margin: 0px 10px 10px 10px !important;
}

.Cont-Spells-Filter
{
  width: 60%;
  height: 100px;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba( 23, 8, 90, 0.3 );
  border-radius: 20px;
  background: rgba( 23, 8, 90, 0.3 );
  margin: 50px;
}

/*Pociones*/

.Cont-Potions
{
  width: 100%;
  /*background-image: url("images/potion_fondo.png");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;*/
  background:
    linear-gradient(324deg, #2f3a36 4%,   transparent 4%) -70px 43px,
    linear-gradient( 36deg, #2f3a36 4%,   transparent 4%) 30px 43px,
    linear-gradient( 72deg, #e3d7bf 8.5%, transparent 8.5%) 30px 43px,
    linear-gradient(288deg, #e3d7bf 8.5%, transparent 8.5%) -70px 43px,
    linear-gradient(216deg, #e3d7bf 7.5%, transparent 7.5%) -70px 23px,
    linear-gradient(144deg, #e3d7bf 7.5%, transparent 7.5%) 30px 23px,

    linear-gradient(324deg, #2f3a36 4%,   transparent 4%) -20px 93px,
    linear-gradient( 36deg, #2f3a36 4%,   transparent 4%) 80px 93px,
    linear-gradient( 72deg, #e3d7bf 8.5%, transparent 8.5%) 80px 93px,
    linear-gradient(288deg, #e3d7bf 8.5%, transparent 8.5%) -20px 93px,
    linear-gradient(216deg, #e3d7bf 7.5%, transparent 7.5%) -20px 73px,
    linear-gradient(144deg, #e3d7bf 7.5%, transparent 7.5%) 80px 73px;
  background-color: #2f3a36;
  background-size: 100px 100px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Cont-Potions-Cards
{
  width: 80%;
  border: 1px solid rgba(8, 90, 59, 0.6);
  border-radius: 20px;
  background: rgba(8, 90, 59, 0.6);
  margin-bottom: 100px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}

.Cont-Potions-Filter
{
  width: 60%;
  height: 100px;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(8, 90, 59, 0.6);
  border-radius: 20px;
  background: rgba(8, 90, 59, 0.6);
  margin: 50px;
}

.Cont-Potion-PotionCard
{
  width: 80%;
  margin: 50px;
  background-color: #1a1919;
  height: fit-content;
  border-radius: 10px;
  border: 1px solid wheat;
  text-align: center;
  font-weight: 900;
  font-size: 20px;
  cursor: pointer;
  padding: 50px;
}

.Cont-Potion-PotionCard-Oculto
{
  display: none;
  flex-wrap: wrap;
}

.Cont-Potion-PotionCard-Oculto-Cont
{
  display: flex;
  flex-wrap: wrap;
}

.Potion-Name
{
  font-size: 30px;
  margin: inherit;
  justify-content: center;
}

.Cont-Potion-PotionCard-Text
{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 10px;
}

.Cont-Potion-PotionCard-Text div
{
  margin-left: 20px;

}

.Potion-Ingredient
{
  width: 100%;
  margin: 0% !important;
}

.Potion-Inventors
{
  width: 49%;
}

.Potion-Ingredients
{
  width: 49%;
}


.Potion-Inventor
{
  width: 100%;
  margin: 0% !important;
}

.Potion-Ingredient-Name 
{
  margin: 0px !important;
  text-align: center;
}

.Potion-InventorName
{
  margin: 0px !important;
  text-align: center;
}

.Potion-characteristics
{
  width: 100%;
}

.Potion-difficulty
{
  width: 30%;
}

.Potion-efect
{
  width: 50%;
}

.Potion-sideEffects
{
  width: 50%;
}

.Potion-Time
{
  width: 30%;
}

.Potion-manufacturer
{
  width: 30%;
}

/*Flecha Atras*/
.FlechaAnterior
{

  font-size: 100px;
  position: absolute;
  top: -13px;
  left: 30px;
  cursor:pointer;
  color:aliceblue;
}

.inputStyle
{
  width: 200px;
  background-color: transparent;
  color: whitesmoke;
  border: 0px solid white;
  border-bottom: 1px solid whitesmoke;
  margin-right: 10px;
  cursor: text;
}

.inputStyle::placeholder
{
  color:whitesmoke;
}

.inputStyle:hover::placeholder
{
  color:rgb(130, 160, 216);
  border: 0px solid white;
  border-bottom: 1px solid rgb(130, 160, 216);
}
.inputStyle:hover
{
  color:rgb(130, 160, 216);
  border: 0px solid white;
  border-bottom: 1px solid rgb(130, 160, 216);
}



.inputStyle:focus
{
  border: 0px solid wheat !important;
}

.botonStyle
{
  width: 50px;
  background-color: transparent;
  color: whitesmoke;
  border: 0px solid;
  font-size: 20px;
  margin-right: 20px;

}

.botonStyle:hover
{
  color:red;
  font-size: 30px;

}

.botonStyle:active
{
  color: rgb(130, 160, 216);
  font-size: 35px;

}